* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html, body {
  height: 100%;
  font-size: 100%;
  font-family: Roboto, sans-serif;
  line-height: 1.4; }

.clearfix::after {
  content: ".";
  display: block;
  clear: both;
  font-size: 0;
  height: 0;
  visibility: hidden; }

img {
  display: block;
  max-width: 100%;
